<template>
  <div>
    <ptheader class="rpublicity_header">个人简历</ptheader>

  <!--  <div contenteditable="true" class="areaDiv" v-html="resume" ></div>-->


    <contentedit   v-model="resume"  class="areaDiv"></contentedit>



    <footer @click="submitBtn()">
      提交
    </footer>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import contentedit from "@/components/contentedit";
  export default {
    data() {
      return {
        resume: ''
      };
    },
    components: {
      contentedit
    },
    methods: {
      submitBtn() {
        let userId = localStorage.getItem("userId");
         var param = new FormData();
          param.append('userId',userId);
          param.append('resume',this.resume);
          let _this = this;
        this.$http({
          method: 'post',
          url: '/org/user/self/app/update/resume',
          data:param
        }).then( res => {
           Toast("保存成功");
           _this.$router.push('/accounts/index');
        })
       }
    },
    created() {
      let _this = this;
      let userId = localStorage.getItem("userId");
      this.$http.get('/org/user/self/get?id='+userId).then(res => {
        _this.resume = res.resume;

      });
     }
  };
</script>
<style scoped>
  .t {
    width: 96%;
    margin: auto;
    display: block;
    margin-top: 2rem;
  }

  #textareas{
    height: 50rem;

  }
  #divhidden {
    visibility: hidden;
  }
  .areaDiv{
    width: 96%;
    margin: auto;
    display: block;
    border:1px solid #dcdcdc;
    min-height: 30rem;
    padding: 1rem;
    margin-top: 0.5rem;
  }
  footer{
    width:96%;
    margin: auto;
    height: 3rem;
    color: white;
    letter-spacing: 0.5rem;
    background: #0b752b;
    line-height: 3rem;
    text-align: center;
    font-size: 16px;
    margin-top: 20px;
    border-radius: 5px;
    margin-bottom: 3rem;
  }

</style>



